@charset "UTF-8";
/* リキッドレイアウト対応 */
:root {
  --base-font-family: 'Noto Serif JP', '游明朝', 'Yu Mincho', YuMincho, 'Hiragino Mincho ProN', 'Hiragino Mincho Pro', serif;
  --second-font-family: 'Libre Franklin', sans-serif;
  --accent-font-family: 'Alex Brush', cursive;
  --padding-pc: 24px;
  --padding-sp: 20px;
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --black: #000;
  --white: #fff;
  --gray: #d8d8d8;
  --gray-light: #f5f5f5;
  --base-color: #2c3240;
  --accent-color: #bd4d51;
  --red: #da1c1c;
}

body {
  font-family: var(--base-font-family);
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
html {
  font-size: 16px;
}
@media (min-width: 768px) {
  html {
    font-size: 0.8333333333vw;
  }
}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  a[href^="tel:"] {
    pointer-events: all;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  height: 100%;
  object-fit: cover;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

html {
  overflow-wrap: break-word;
}

.catch-bg-base {
  width: fit-content;
  padding: 0.1875rem 0.125rem 0;
  background: linear-gradient(to bottom, transparent 0 0.0625rem, var(--base-color) 0.0625rem 5.5rem, transparent 5.5rem 100%);
  position: relative;
}

.header {
  position: fixed;
  z-index: 1000;
  top: 2.5rem;
  right: 0rem;
  z-index: 100;
}

.header__nav {
  background-color: var(--base-color);
  display: flex;
  align-items: center;
  padding: 0 2.5rem;
}

.header__nav-list {
  display: flex;
}

.header__nav-item {
  position: relative;
}
.header__nav-item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 8.75rem;
  height: 0.125rem;
  background-color: var(--red);
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media (any-hover: hover) {
  .header__nav-item:hover::after {
    opacity: 1;
  }
}

.header__nav-link {
  display: block;
  padding: 0.8125rem 0.9375rem;
  min-width: 8.75rem;
  text-align: center;
  font-family: var(--base-font-family);
  font-size: 1rem;
  color: var(--white);
  line-height: 1.8;
  transition: opacity 0.3s ease;
}
@media (any-hover: hover) {
  .header__nav-link:hover {
    opacity: 0.7;
  }
}

.inner {
  max-width: 90.625rem;
  padding: 0 var(--padding-pc);
  margin-inline: auto;
}
@media screen and (max-width: 767px) {
  .inner {
    max-width: 37.5rem;
    padding: 0 var(--padding-sp);
  }
}

.svg-page .fv__icon {
  width: 31.25rem;
  height: auto;
  aspect-ratio: 130/136;
  margin-inline: auto;
  margin-top: 12.5rem;
}

.top-content {
  overflow: hidden;
  --section-margin: rem(100);
}

.grids {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  display: grid;
  grid-template-columns: 2.5rem 1fr 1fr 1fr 1fr 1fr 1fr 2.5rem;
  gap: 1px;
  background-color: var(--gray);
}

.grid {
  background-color: var(--gray-light);
  height: 100%;
}

.fv {
  position: relative;
  z-index: 2;
  width: 100%;
}

.fv__inner {
  max-width: 120rem;
  padding: 0 2.5rem;
  margin-inline: auto;
}

.fv__main-visual {
  position: absolute;
  top: -5.625rem;
  right: -11.5rem;
  width: 67.1875rem;
  aspect-ratio: 1075/1972;
  z-index: 1;
}

.fv__main-visual-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fv__bg {
  position: absolute;
  top: 43.3125rem;
  left: -1.1875rem;
  width: 121.875rem;
  height: 46.3125rem;
  z-index: 0;
}

.fv__bg-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fv__content {
  padding-top: 9.375rem;
  display: flex;
  align-items: flex-end;
  gap: 12.1875rem;
}

.fv__catch {
  min-width: 43.75rem;
  width: fit-content;
  margin-bottom: 10.25rem;
}
.fv__catch span {
  display: inline-block;
}

.fv__catch-title {
  position: relative;
  z-index: 1;
}

.fv__catch-line {
  display: block;
  line-height: 1.1;
}

.fv__catch-line--1 {
  margin-bottom: 1rem;
}

.fv__catch-line--2 {
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.fv__catch-line--3 {
  margin-top: -0.625rem;
  margin-bottom: 1rem;
}

.fv__catch-text {
  font-family: var(--second-font-family);
  font-size: 5.5375625rem;
  font-weight: var(--regular);
  line-height: 1.1;
}

.fv__catch-text--white {
  color: var(--white);
  line-height: 4rem;
}

.fv__catch-text--dark {
  color: var(--base-color);
}

.fv__catch-text--accent {
  font-family: var(--accent-font-family);
  font-size: 7.3426875rem;
  color: var(--accent-color);
}

.fv__right {
  position: relative;
  z-index: 1;
}

.fv__text-right-desc {
  font-family: var(--base-font-family);
  font-size: 0.8125rem;
  color: var(--base-color);
  line-height: 1.8;
  letter-spacing: 0.1em;
  margin-bottom: 1.875rem;
}

.fv__text-right-bottom {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 2.1875rem;
}

.fv__text-right-lead {
  font-family: var(--second-font-family);
  font-size: 0.875rem;
  color: var(--base-color);
  line-height: 1.8;
  text-align: center;
  margin-bottom: 1.25rem;
  width: 50%;
  flex-grow: 1;
}

.fv__icon {
  display: block;
  width: 8.125rem;
  height: 8.5rem;
}

.fv__logo-img {
  width: 100%;
  height: 100%;
}

.read {
  margin-top: -1.875rem;
  position: relative;
  z-index: 1;
  padding-top: 4.4375rem;
  background-image: url("../images/read_bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
}

.read__inner {
  padding: 0 2.5rem;
  max-width: 120rem;
  margin-inline: auto;
}

.read__contents {
  display: grid;
  grid-template-columns: 17.1875rem 19.5625rem;
  gap: 2rem 2.4375rem;
}

.read__thumbnail {
  aspect-ratio: 275/344;
  max-width: 17.1875rem;
  margin-left: auto;
}

.read__thumbnail--right {
  grid-column: 2;
}

.read__text-left {
  max-width: 19.5625rem;
}

.read__text-left-desc {
  font-family: var(--base-font-family);
  font-size: 0.8125rem;
  color: var(--white);
  line-height: 2.12;
  letter-spacing: 0.11em;
}

.read__thumbnail-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}